.container {
	max-width: 1220px;
	margin: 0 auto;
	padding: 0 25px;
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.row>[class^=col-] {
	flex-basis: 0;
	flex-grow: 1;
	padding-left: 15px;
	padding-right: 15px;
}

.col-12 {
	flex-basis: 100%;
	max-width: 100%;
}

.col-6 {
	flex-basis: 50%;
	max-width: 50%;
}

.col-4 {
	flex-basis: 33.3333%;
	max-width: 33.3333%;
}

.col-3 {
	flex-basis: 25%;
	max-width: 25%;
}

.col-2 {
	flex-basis: 16.6666%;
	max-width: 16.6666%;
}